* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  padding: 20px;
}

.App {
  max-width: 800px;
  margin: 0 auto;
}

.container {
  background: white;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}

.header {
  text-align: center;
  margin-bottom: 30px;
}

.header h1 {
  color: #333;
  font-size: 2rem;
  margin-bottom: 10px;
}

.header h2 {
  color: #666;
  font-size: 1.5rem;
  font-weight: normal;
}

.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  color: #333;
  font-size: 1.1rem;
}

.form-control {
  width: 100%;
  padding: 12px 15px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-control:focus {
  outline: none;
  border-color: #667eea;
}

.btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 8px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: transform 0.2s ease;
  width: 100%;
  font-weight: bold;
}

.btn:hover {
  transform: translateY(-2px);
}

.btn:active {
  transform: translateY(0);
}

.user-info {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 25px;
}

.user-info p {
  margin-bottom: 8px;
  color: #555;
}

.timer {
  background: #ff6b6b;
  color: white;
  padding: 10px 20px;
  border-radius: 25px;
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 25px;
  display: inline-block;
}

.question-card {
  background: #f8f9fa;
  padding: 25px;
  border-radius: 10px;
  margin-bottom: 25px;
}

.question-number {
  font-size: 1.3rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 15px;
}

.text-answer {
  width: 100%;
  min-height: 120px;
  padding: 15px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  resize: vertical;
}

.text-answer:focus {
  outline: none;
  border-color: #667eea;
}

.results-success {
  background: #d4edda;
  color: #155724;
  padding: 25px;
  border-radius: 10px;
  text-align: center;
  margin: 25px 0;
}

.results-success h3 {
  margin-bottom: 10px;
  font-size: 1.5rem;
}

.math-input-container {
  border: 2px solid #e9ecef;
  border-radius: 10px;
  padding: 15px;
  background: #f8f9fa;
  margin: 15px 0;
}

.loading {
  text-align: center;
  padding: 40px;
}

.spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #667eea;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media (max-width: 768px) {
  body {
    padding: 10px;
  }
  
  .container {
    padding: 20px;
  }
  
  .header h1 {
    font-size: 1.6rem;
  }
  
  .header h2 {
    font-size: 1.3rem;
  }
  
  .timer {
    font-size: 1.1rem;
    padding: 8px 16px;
  }
}

@media (max-width: 480px) {
  .header h1 {
    font-size: 1.4rem;
  }
  
  .form-control {
    padding: 10px 12px;
  }
  
  .btn {
    padding: 12px 20px;
    font-size: 1rem;
  }
}
